	module.exports = {"data":{"markdownRemark":{"html":"<p>ウチの近所の神社では毎年節分にちょっと変わった祭りを行います。 まぁどこでも同じように節分の日になるとその神社でも豆を撒くのですが、そこだけ何故か掛け声が「鬼は内、福は内」。実はそこの神社、世にも珍しい鬼を祀っている神社で毎年節分になると川に綿流ｓ…ｹﾞﾌﾝｹﾞﾌﾝ、鬼は内、福は内と掛け声を上げて豆をまくそうで。また金棒を奉納すると願い事が叶うと言われていて神社の裏手には金棒がたくさん置かれていてちょっと物々しい雰囲気。  なにぶん近くに店といったらコンビニと薬局・スーパーのチェーン店しかないくらいのド田舎なので普段は近隣住民以外皆無ですが珍しいせいかその日だけひっくり返したような賑わいになります。 もし暇でしたら来てみてはいかがでしょうか？＿？</p>","frontmatter":{"cover":null,"date":"2006-01-29T04:56:00+09:00","strDate":"2006/1/28","title":"地元のちょっと変わった祭"}}},"pathContext":{"path":"/ja/blog/2006/01/29-地元のちょっと変わった祭/","langKey":"ja"}}

